const initState = {

}

const authReducer = (state= initState, action) => {
     // eslint-disable-next-line default-case
     switch(action.type){
         
          // eslint-disable-next-line no-fallthrough
          case 'LOGIN_ERROR' :
               console.log(' Nie Udao si zalogowa :(');
               return{
                    ...state,
                    authError: 'Co wpisae nie tak :/'
               }
          case 'LOGIN_SUCCESS':
               console.log('Udao si zalogowa :)');
               return{
                    ...state,
                    authError: null
               }
          case 'SIGNOUT_SUCCESS':
               console.log('DO zobaczenia niedugo :)');
               return state;

          case 'SIGNOUT_ERROR':
               console.log('Co nie dziaa Wylogowywanie :(');
               return state;

          case 'SIGNUP_SUCCESS':
               console.log('Zarejstrowae si bez problemw :P');
               return{
                    ...state,
                    authError: null
               }
          case 'SIGNUP_ERROR':
               console.log('Co poszo nie tak przy restracji stary :/');
               return{
                    ...state,
                    authError: action.err.message
               }
               
          default :
               return state;
     }
     
}

export default authReducer